<template>
  <div class="cms-list-layout">
    <cms-tabs>
      <cms-tab to="/shopManage/list">店铺列表</cms-tab>
      <cms-tab to="/shopManage/enterApplication">入驻申请</cms-tab>
    </cms-tabs>
    <cms-prompt collapse='true'>
      <li>显示入住店铺信息，可进行新增、修改、删除及查看等操作</li>
      <li> 营业中表示店铺正常使用中；暂停营业表示商家可登录商家中心，但无法上架商品；关闭中表示商家无法登陆商家中心</li>
    </cms-prompt>
    <!-- 筛选区 -->
    <section class="cms-list-query">
      <div class="query-group">
        <label>店铺名称</label>
        <el-input v-model="queryParams.storeName" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <label>商家账号</label>
        <el-input v-model="queryParams.storeUsername" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <label>店铺等级</label>
        <el-select v-model="queryParams.levelId" @change="query">
          <el-option label="全部" value=""></el-option>
          <el-option v-for="item in gradeList" :key="item.id" :label="item.levelName" :value="item.id"></el-option>
        </el-select>
      </div>
      <div class="query-group">
        <label>店铺类型</label>
        <el-select v-model="queryParams.storeType" @change="query">
          <el-option label="全部" value=""></el-option>
          <el-option label="个人店铺" :value="'1'"></el-option>
          <el-option label="企业店铺" :value="'2'"></el-option>
        </el-select>
      </div>
      <div class="query-group">
        <label>主营类目</label>
        <el-select v-model="queryParams.saleCateId" value-key="id" @change="query">
          <el-option label="全部" value=""></el-option>
          <el-option :label="item.cateName" :value="item.id" v-for="item in categoryItems" :key="item.id">
          </el-option>
        </el-select>
      </div>
      <div class="query-group" @change="query">
        <label>开店时间</label>
        <cms-picker @change="setTimer" value-format="yyyy-MM-dd HH:mm:ss"></cms-picker>
      </div>
      <div class="query-group">
        <label>状态</label>
        <el-select v-model="queryParams.status" @change="query">
          <el-option label="全部" value=""></el-option>
          <el-option label="正常营业" value="1"></el-option>
          <el-option label="暂停营业" value="2"></el-option>
          <el-option label="停止营业" value="3"></el-option>
        </el-select>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
      <el-table-column type="selection" width="46" fixed='left'></el-table-column>
      <el-table-column prop="id" label="id" width="60"></el-table-column>
      <el-table-column prop="storeName" label="店铺名称" min-width='160'></el-table-column>
      <el-table-column prop="storeUsername" label="商家账号" min-width="160"></el-table-column>
      <el-table-column prop="storeType" label="店铺类型" min-width="90">
        <div slot-scope="scope">
          <span class="error" v-if="scope.row.storeType === 1">个人店铺</span>
          <span class="success" v-else-if="scope.row.storeType === 2">企业店铺</span>
        </div>
      </el-table-column>
      <el-table-column prop="storeLevel.levelName" label="店铺等级" min-width="80"></el-table-column>
      <el-table-column prop="productCategory.cateName" label="主营类目" min-width="180">
      </el-table-column>
      <el-table-column prop="createTime" label="开店时间" min-width="180">
        <template slot-scope="scope">
          {{$moment(scope.row.createTime)}}
        </template>
      </el-table-column>
      <el-table-column prop="storeInfo.address" label="所在地" min-width="180"></el-table-column>
      <el-table-column prop="enabled" label="状态" width="110">
        <div slot-scope="scope">
          <span class="success" v-if="scope.row.status==1">营业中</span>
          <span v-if="scope.row.status==2" style="color:#FF9966;">暂停营业</span>
          <span v-if="scope.row.status==3" style="color:#B2B2B2;">关闭营业</span>
        </div>
      </el-table-column>
      <el-table-column label="操作" width='320' fixed="right" align="center">
        <div slot-scope="scope">
          <cms-button icon='edit' type='info' size='small' @click.stop="$routerLink('/shopManage/shopCategory/list','update',{id:scope.row.id})">查看店铺分类</cms-button>
          <cms-button icon='edit' size='small' type='info' @click.stop="$routerLink('/shopManage/view','update',{id:scope.row.id,storeType:scope.row.storeType})">查看</cms-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
</template>
<script>
import list from '@/mixins/list'
import { shopManageList, shopManageDelete } from '@/api/shop/shopManage.js'

import { gradeList } from '@/api/shop/shopLevel.js'

import { productCategoryList } from '@/api/product/category.js'
var defaultData = {
  storeName: '',
  levelId: '',
  storeType: '',
  saleCateId: '',
  startTime: '',
  endTime: '',
  status: '',
  storeUsername: '',
  page: 1,
  size: 10
}
export default {
  mixins: [list],
  data() {
    return {
      gradeList: [],
      categoryItems: [], // 商品分类列表
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {
    deleteData(id) {
      // 包装一层删除的操作
      this.deleteTableData(shopManageDelete, id) // 这个才是真实的删除方法
    },
    deleteDatas() {
      this.deleteTableDatas(shopManageDelete, this.ids) // 这个才是真实的删除方法
    },
    handleCommand(command) { // 个人企业店铺添加
      this.$routerLink('/shopManage/companysave', 'save', { type: command })
    },
    getgradeList() {
      // 获取店铺等级列表
      gradeList({ size: 999999, page: 0 }).then(res => {
        if (res.code === 200) {
          this.gradeList = res.data.content
        }
      })
    },
    getMenuTree() {
      // 获取分类节点
      productCategoryList().then(res => {
        this.categoryItems = res.data
      })
    },
    setTimer(timer) {
      // 设置时间区间
      this.queryParams.startTime = timer[0]
      this.queryParams.endTime = timer[1]
      this.query()
    },
    getAll() {
      this.getMenuTree()
      this.getgradeList()
    }
  },
  created() {
    // 首次触发
    this.getTableData(shopManageList, this.queryParams)
    this.getAll()
  }
}
</script>

<style>
.pull-btn {
  width: 63px;
  margin-bottom: 10px;
  margin-right: 40px;
  line-height: 5px;
}
.pull-btn-add {
  height: 28px;
}

</style>



